<template>
  <div>
    根据部门查询：
    <el-select v-model="value" placeholder="请选择" >
      <el-option
              v-for="item in DepartmentOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
      </el-option>
    </el-select>
    根据工单类型查询：
    <el-select v-model="value" placeholder="请选择">
      <el-option
              v-for="item in typeOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
      </el-option>
    </el-select>
    <br><br>
    <el-button @click="bathAddAsset" type="success"
               :disabled="this.selectionList.length == 0">入库</el-button>
    &nbsp
    <el-button @click="bathDeleteAsset" type="danger"
               :disabled="this.selectionList.length == 0">删除</el-button>
    <br><br>
    <el-table :data="tableData" border style="width: 100%" @selection-change="selectionChange">
      <el-table-column type="selection" width="55">
      </el-table-column>
      <el-table-column fixed type="index" label="序号" width="100">
      </el-table-column>
      <el-table-column prop="username" label="用户名" width="150">
      </el-table-column>
      <el-table-column prop="department" label="部门" width="180">
      </el-table-column>
      <el-table-column prop="area" label="楼层" width="180">
      </el-table-column>
      <el-table-column prop="applicationTime" label="提交时间" width="180">
      </el-table-column>
      <el-table-column prop="questionTypeOne" label="工单类型" width="180">
      </el-table-column>
      <el-table-column prop="questionTypeTwo" label="工单类型2" width="180">
      </el-table-column>
      <el-table-column prop="orderDescription" label="工单详情描述" width="180">
      </el-table-column>
      <el-table-column fixed="right" label="操作">
        <template slot-scope="scope">
          <el-button @click="addAssetToOA(scope.row)" type="info" size="small"icon="el-icon-upload">入库</el-button>
          <el-button @click="handleEdit(scope.$index,scope.row)" type="info" size="small" icon="el-icon-edit">编辑</el-button>
          <el-button @click="deleteAsset(scope.row)" type="danger" size="small"icon="el-icon-delete">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
            background
            layout="prev, pager, next"
            :page-size= "pageSize"
            :total="total"
            :current-page="currentPage"
            @current-change="pageChange">
    </el-pagination>
    <!-- ’编辑‘弹框界面-->
    <el-dialog :visible.sync="dialogFormVisible" :close-on-click-modal="false" style="width: 100%" title="编辑资产信息" >
      <el-form :model="editForm" ref="editForm">
        <el-form-item style="width: 80%" label="资产id" prop="id" >
          <el-input v-model="editForm.id" ></el-input>
        </el-form-item>
        <el-form-item style="width: 80%" label="工号">
          <el-input v-model="editForm.jobNumber" ></el-input>
        </el-form-item>
        <el-form-item style="width: 80%" label="设备名称" >
          <el-input v-model="editForm.deviceName"></el-input>
        </el-form-item>
        <el-form-item style="width: 80%" label="设备编号" >
          <el-input v-model="editForm.assetNumber"></el-input>
        </el-form-item>
        <el-form-item style="width: 80%" label="是否入库" >
          <el-input v-model="editForm.status"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button @click="resetForm('editForm')">重置</el-button>
        <el-button type="primary" @click="updateAsset">确 定</el-button>
      </div>
    </el-dialog>
  </div>

</template>

<script>
  export default {
    inject: ['reload'], //依赖注入
    methods: {
      pageChange(currentPage){
        const that = this
        that.currentPage = currentPage;
        that.getList(that)
      },
      //批量选择时触发
      selectionChange(val){
        this.selectionList = val
      },
      getList(that){
        axios.get(this.baseUrl+'/listAllItOrder/'+that.currentPage+'/5').then(function (response) {
          that.tableData = response.data.content
          that.total = response.data.totalElements
        })
      },
      listByStatus(command){
        const that = this
        axios.request({
          url: this.baseUrl+'/listByStatus',
          params:{
            status:command,
            page:that.currentPage,
            size:10
          }
        }).then((response) =>{
          that.tableData = response.data.content
          that.total = response.data.totalElements
        })
      },
      findByJobNumber(){
        let i = parseInt(this.jobNumber)
        const that = this
        axios.get(this.baseUrl+'/findByJobNumber?jobNumber='+i).then((response)=>{
          that.tableData = response.data
          that.total = response.data.totalElements
        })
      },
      findByAssetNumber(){
        let str = this.assetNumber
        axios.get(this.baseUrl+'/findByAssetNumber?assetNumber='+str).then((response)=>{
          this.tableData = response.data
          this.total = response.data.totalElements
        })
      },
      handleEdit(index,row){
        this.dialogFormVisible = true;
        this.editForm = Object.assign({},row);
      },
      updateAsset(){
        let para = Object.assign({},this.editForm);
        axios.post(this.baseUrl+'/updateAsset',para).then((response)=>{
          if(response.data.success == true){
            this.$alert('资产信息修改成功!','提示', {
              confirmButtonText: '确定',
              callback: action => {
                this.reload();
              }
            });
          }
        })
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      deleteAsset(row){
        this.$confirm('此操作将永久删除所选中信息, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          axios.delete(this.baseUrl+'/deleteAsset/?id='+row.id)
          this.$alert('删除成功!','提示', {
            confirmButtonText: '确定',
            callback: action => {
              this.reload();
            }
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      },
      bathDeleteAsset() {
        let assetIds = this.selectionList.map(item => item.id).join()
        this.$confirm('此操作将永久删除所选中信息, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          axios.post(this.baseUrl+'/bathDeleteAsset/?assetIds=' + assetIds)
          this.$alert('删除成功!','提示', {
            confirmButtonText: '确定',
            callback: action => {
              this.reload(); //调用注入的方法达到刷新router-view的目的
              // window.location.reload()
            }
          });
          this.listByStatus("否");
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '已取消删除'
            });
          });
      },
    },
    created() {
      const this1 = this
      this1.getList(this1)
    },
    data() {
      return {
        baseUrl: 'http://103.228.163.54:9001',
        DepartmentOptions:[],
        typeOptions:[],
        input:'',
        jobNumber:'',
        assetNumber:'',
        selectionList:'',
        pageSize: 10,
        total: 0,
        tableData: [],
        currentPage: 1,
        checked: true,
        dialogFormVisible: false,
        editForm: {
            id:'',
            jobNumber:'',
            deviceName: '',
            assetNumber:'',
            status: ''
        },
      }
    }
  }
</script>
