<template>
  <div>
    根据楼层查询：
    <el-select v-model="value" placeholder="请选择" @change="findByArea">
      <el-option
              v-for="item in areaOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
      </el-option>
    </el-select>
    根据工单类型查询：
    <el-select v-model="value" placeholder="请选择" @change="findByQT1" >
      <el-option
              v-for="item in typeOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
      </el-option>
    </el-select>
    <br><br>
    <el-table :data="tableData" border style="width: 100%" @selection-change="selectionChange">
      <el-table-column type="selection" width="55">
      </el-table-column>
      <el-table-column fixed type="index" label="序号" width="100">
      </el-table-column>
      <el-table-column prop="username" label="用户名" width="150">
      </el-table-column>
      <el-table-column prop="department" label="部门" width="180">
      </el-table-column>
      <el-table-column prop="area" label="楼层" width="120">
      </el-table-column>
      <el-table-column prop="seat" label="座位号" width="120">
      </el-table-column>
      <el-table-column prop="applicationTime" label="提交时间" width="180">
      </el-table-column>
      <el-table-column prop="questionTypeOne" label="工单类型" width="180">
      </el-table-column>
      <el-table-column prop="questionTypeTwo" label="工单类型2" width="180">
      </el-table-column>
      <el-table-column prop="orderDescription" label="工单详情描述" width="180">
      </el-table-column>
      <el-table-column prop="images" label="图片" width="180">
      </el-table-column>
      <el-table-column fixed="right" label="操作">
        <template slot-scope="scope">
          <el-button @click="handleEdit(scope.$index,scope.row)" type="info" size="small" icon="el-icon-edit">编辑</el-button>
          <el-button @click="deleteAsset(scope.row)" type="danger" size="small"icon="el-icon-delete">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
            background
            layout="prev, pager, next"
            :page-size= "pageSize"
            :total="total"
            :current-page="currentPage"
            @current-change="pageChange">
    </el-pagination>
  </div>

</template>

<script>
  import * as dateUtils from "element-ui";

  export default {
    inject: ['reload'], //依赖注入
    methods: {
      pageChange(currentPage){
        const that = this
        that.currentPage = currentPage;
        that.getList(that)
      },
      //批量选择时触发
      selectionChange(val){
        this.selectionList = val
      },
      getList(that){
        axios.get(this.baseUrl+'/listAllItOrder/'+that.currentPage+'/5').then(function (response) {
          that.tableData = response.data.content
          that.total = response.data.totalElements
          console.log(this.$moment(new Date()).format('YYYY-MM-DD  HH:mm:ss'))
        })
      },
      listByStatus(command){
        const that = this
        axios.request({
          url: this.baseUrl+'/listByStatus',
          params:{
            status:command,
            page:that.currentPage,
            size:10
          }
        }).then((response) =>{
          that.tableData = response.data.content
          that.total = response.data.totalElements
        })
      },
      findByArea(){
        let i = parseInt(this.jobNumber)
        const that = this
        axios.get(this.baseUrl+'/findByArea?area='+i).then((response)=>{
          that.tableData = response.data
          that.total = response.data.totalElements
        })
      },
      findByQT1(){
        let str = this.assetNumber
        axios.get(this.baseUrl+'/findByQT1?qt1='+str).then((response)=>{
          this.tableData = response.data
          this.total = response.data.totalElements
        })
      },
      handleEdit(index,row){
        this.dialogFormVisible = true;
        this.editForm = Object.assign({},row);
      },
      updateAsset(){
        let para = Object.assign({},this.editForm);
        axios.post(this.baseUrl+'/updateAsset',para).then((response)=>{
          if(response.data.success == true){
            this.$alert('资产信息修改成功!','提示', {
              confirmButtonText: '确定',
              callback: action => {
                this.reload();
              }
            });
          }
        })
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      deleteAsset(row){
        this.$confirm('此操作将永久删除所选中信息, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          axios.delete(this.baseUrl+'/deleteAsset/?id='+row.id)
          this.$alert('删除成功!','提示', {
            confirmButtonText: '确定',
            callback: action => {
              this.reload();
            }
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      },
      bathDeleteAsset() {
        let assetIds = this.selectionList.map(item => item.id).join()
        this.$confirm('此操作将永久删除所选中信息, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          axios.post(this.baseUrl+'/bathDeleteAsset/?assetIds=' + assetIds)
          this.$alert('删除成功!','提示', {
            confirmButtonText: '确定',
            callback: action => {
              this.reload(); //调用注入的方法达到刷新router-view的目的
              // window.location.reload()
            }
          });
          this.listByStatus("否");
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      },
      //格式化日期
      formatDate(dateStr){
        let date = new Date(dateStr);
        return dateUtils.formatDate(date,'yyyy-MM-dd hh:mm:ss');
      },
    },
    created() {
      const this1 = this
      this1.getList(this1)
    },
    data() {
      return {
        baseUrl: 'http://103.228.163.54:9001',
        areaOptions: [{
          value: '选项1',
          label: '12'
        },{
          value: '选项2',
          label: '13'
        },{
          value: '选项3',
          label: '14'
        },{
          value: '选项4',
          label: '17'
        },{
          value: '选项5',
          label: '18'
        },{
          value: '选项6',
          label: '19'
        },{
          value: '选项7',
          label: '20'
        },{
          value: '选项8',
          label: '21'
        },{
          value: '选项9',
          label: '22'
        },{
          value: '选项10',
          label: '23'
        }],
        typeOptions:[{
          value: '选项1',
          label: '网络'
        },{
          value: '选项2',
          label: '邮箱'
        },{
          value: '选项3',
          label: '电脑'
        },{
          value: '选项4',
          label: '配件申请/领取'
        },{
          value: '选项5',
          label: '笔记本报销'
        },{
          value: '选项6',
          label: '资源管理'
        }],
        input:'',
        jobNumber:'',
        assetNumber:'',
        selectionList:'',
        pageSize: 10,
        total: 0,
        tableData: [],
        currentPage: 1,
        checked: true,
        dialogFormVisible: false,
        editForm: {
          id:'',
          jobNumber:'',
          deviceName: '',
          assetNumber:'',
          status: ''
        },
      }
    }
  }
</script>
